body {
	background-color: #d9e8f7;
	height: 100%;
	font-size: 1.3vw;
}

h1 {
	font-size: 3vw;
	padding-left: 10px;
	padding-right: 10px;
}

h2 {
	font-size: 2.5vw;
	padding-left: 10px;
	padding-right: 10px;
}

.headerGroup-appear {
  opacity: 0.01;
}

.headerGroup-appear.headerGroup-appear-active {
  opacity: 1;
  -webkit-transition: opacity 1s ease-in;
  -o-transition: opacity 1s ease-in;
  transition: opacity 1s ease-in;
}

.App {
	margin-top: 10vh;
	text-align: center;
}

.unselectedButton {
	font-weight: normal;
	margin-left: .5vw;
	margin-right: .5vw;
	margin-bottom: 1.5vh;
	cursor: pointer;
}

.selectedButton {
	font-weight: bold;
	margin-left: .5vw;
	margin-right: .5vw;
	margin-bottom: 1.5vh;
	cursor: pointer;
}

.linkIcon {
	padding-left: .5vw;
}

.repoIcon {
	padding-right: 5px;
}

.socialMediaDiv {
	text-align:center;
}

.socialMediaButton {
	font-size: 10vw;
	padding-left: 2vw;
	padding-right: 2vw;
}

.buttonGroup-appear {
	-webkit-transform: translate(100%);
	    -ms-transform: translate(100%);
	        transform: translate(100%);
}

.buttonGroup-appear.buttonGroup-appear-active {

	-webkit-transform: translate(0%);

	    -ms-transform: translate(0%);

	        transform: translate(0%);
	-webkit-transition: -webkit-transform 1000ms ease-in;
	transition: -webkit-transform 1000ms ease-in;
	-o-transition: transform 1000ms ease-in;
	transition: transform 1000ms ease-in;
	transition: transform 1000ms ease-in, -webkit-transform 1000ms ease-in;
}

.portraitImage {
	display: block;
	margin-left: auto;
	margin-right: auto;
	border-radius: 50%;
	width: 15vw;
	height: 15vw;
}

.navigator {
	margin-top: 3vh;
}

.navigatorGroup-appear {
	-webkit-transform: scale(.1, .1);
	    -ms-transform: scale(.1, .1);
	        transform: scale(.1, .1);
}

.navigatorGroup-appear.navigatorGroup-appear-active {

	-webkit-transform: scale(1, 1);

	    -ms-transform: scale(1, 1);

	        transform: scale(1, 1);
	-webkit-transition: -webkit-transform 500ms ease-in;
	transition: -webkit-transform 500ms ease-in;
	-o-transition: transform 500ms ease-in;
	transition: transform 500ms ease-in;
	transition: transform 500ms ease-in, -webkit-transform 500ms ease-in;
}

.navigatorPage {
	max-width: 60%;
	min-height: 600px;
	margin-top: 2vh;
	margin-bottom: 30vh;
	margin-left: auto;
	margin-right:auto;
	text-align: left;
	padding-left: 1vw;
	padding-right: 1vw;
}

thead {
	font-weight: bold;
}

.errorEntry {
	color: red;
}

footer {
	width: 100%;
	background: #d5d5d5;
}

.footer-container {
	padding-top: 3vh;
	padding-bottom: 3vh;
	text-align:center;
}

@media (max-width: 1200px) {

	body {
		font-size: 14px;	
	}

	h1 {
		font-size: 32px;
	}

	h2 {
		font-size: 24px;
	}

	.portraitImage {
		width: 175px;
		height: 175px;
	}

	.socialMediaButton {
		font-size: 120px;
		padding-left: 25px;
		padding-right: 25px;
	}
}

@media (min-width: 1200px) {
	
	.unselectedButton, .selectedButton {
		font-size: 1.2vw;
		width:10vw;
		height: 5vh;
	}

}

@media (max-width: 800px) {

	.navigatorPage {
		max-width: 90%;
	}

}

@media (max-width: 500px) {

	.repoStats {
		display: none;
	}

}



